:root {
	/* COLORS */
	--white: #242323;
	--purple: #7800A2;
	--lightpurple: #711792;

	/* RADII */
	--button-radius: 0.6rem;

	/* SIZES */
	--max-width: 658px;
	--max-height: 520px;

	font-size: 16px;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.body_form {
	align-items: center;
	background-color: var(--white);
	background: url("../../assets/GameZone\ Wp.png");
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	display: grid;
}

.form__title {
	font-weight: 600;
	color: #ffffff;

}

.link {
	color: rgb(202, 194, 194);
	font-size: 0.9rem;
	margin: 1.5rem 0;
	text-decoration: none;
}

.container {
	margin: auto;
	margin-top: 100px;
	margin-bottom: 700px;
	margin-right: 10%;
	background-color: var(--white);
	border-radius: 30px;
	box-shadow: 0 0.9rem 1.7rem rgba(0, 0, 0, 0.25),
		0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
	height: var(--max-height);
	max-width: var(--max-width);
	overflow: hidden;
	position: relative;
	width: 100%;
}

.container__form {
	
	height: 100%;
	position: absolute;
	top: 0;
	transition: all 0.6s ease-in-out;
}

.container--signin {
	
	left: 0;
	width: 50%;
	z-index: 2;
}

.container.right-panel-active .container--signin {
	transform: translateX(100%);
	
}

.container--signup {
	left: 0;
	opacity: 0;
	width: 50%;
	z-index: 1;
	
}

.container.right-panel-active .container--signup {
	animation: show 0.6s;
	opacity: 1;
	transform: translateX(100%);
	z-index: 5;
	
}

.container__overlay {
	height: 100%;
	left: 50%;
	overflow: hidden;
	position: absolute;
	top: 0;
	transition: transform 0.6s ease-in-out;
	width: 50%;
	
	z-index: 100;
}

.container.right-panel-active .container__overlay {
	transform: translateX(-100%);
}

.overlay {
	background: url("../../assets/Container.png");
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 100%;
	left: -100%;
	position: relative;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
	width: 200%;
	
}

.container.right-panel-active .overlay {
	transform: translateX(50%);
	
}

.overlay__panel {
	align-items: center;
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: center;
	position: absolute;
	text-align: center;
	top: 0;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
	width: 50%;
	
}

.overlay--left {
	transform: translateX(-20%);
	
}

.container.right-panel-active .overlay--left {
	transform: translateX(0);
	
}

.overlay--right {
	right: 0;
	transform: translateX(0);
}

.container.right-panel-active .overlay--right {
	transform: translateX(20%);
}

.btn {
	background-image: linear-gradient(90deg, var(--purple) 0%, var(--lightpurple) 74%);
	border-radius: 20px;
	border: 1px solid var(--purple);
	color: white;
	cursor: pointer;
	font-size: 0.8rem;
	font-weight: bold;
	letter-spacing: 0.1rem;
	padding: 0.9rem 4rem;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	margin-bottom: 20px;
}

.form .btn {
	margin-top: 1.5rem;
}

.btn:active {
	transform: scale(1.95);
}

.btn:focus {
	outline: none;
	color: #000000;
}

.btnTwo {
	color: #ffffff;
	font-size: 20px;
	margin: 5px;
	cursor: pointer;
}

.form {
	background-color: var(--white);
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 3rem;
	height: 100%;
	text-align: center;

}

.input {
	background-color: #fff;
	border-radius: 12px;
	padding: 0.9rem 0.9rem;
	margin: 0.5rem 0;
	width: 100%;
}
.errorMessage{
	color: red;
}

@keyframes show {

	0%,
	49.99% {
		opacity: 0;
		z-index: 1;
	}

	50%,
	100% {
		opacity: 1;
		z-index: 5;
	}
}